import React, {Component} from 'react';
import 'semantic-ui-css/semantic.min.css';
import {Container, Header, Grid, Image, Segment} from 'semantic-ui-react';
import '.././styles.css';

class Homepage extends Component{
	render(){
		return (
			<Segment inverted basic>
				<Container className="whoop">
					<Container  textAlign='right'>
						<Header as='h1'
								className='ui color1'
								style = {{fontSize:'4em'}}>
							Zack Bermack
							<Header as='h2'
									className='ui color1'
									content='Filmmaker'
							/>
						</Header>
					</Container>
					<Container className='parbreak'>
						<Grid container stackable columns='equal' verticalAlign='middle'>
							<Grid.Row>
								<Grid.Column width={8}>
									<p className="mainsize">
										Zack Bermack is a writer, director, editor, cinematographer, and gaffer.
										Zack majored in Film Studies and Philosophy at Connecticut College, and has
										deeply synthesized his love of philosophy with his love of film. Because of
										Zack’s combination of academic interests and his education at Connecticut
										College, he has a unique perspective on ideology in film.
									</p>
								</Grid.Column>
								<Grid.Column width={8}>
									<Image className='ui shadowimage' centered bordered src={require('../Images/pensivezack.JPG')} size='large'/>
								</Grid.Column>
							</Grid.Row>
							<Grid.Row>
								<Grid.Column width={8}>
									<Image className='ui shadowimage' centered bordered src={require('../Images/zacklight.jpg')} size='large'/>
								</Grid.Column>
								<Grid.Column width={8}>
									<p className="mainsize">
										The esteemed professors at Connecticut College prioritize ideological
										representation in filmmaking and Zack has taken these values to heart.
										Representations of race, class, gender, sexuality, religion are on the
										forefront of his creative mind. Zack’s films have been nominated for
										numerous awards and accepted to a wide range of film festivals.
									</p>
								</Grid.Column>

							</Grid.Row>
						</Grid>
						<Grid>
							<Grid.Row columns={2}>
								<Grid.Column verticalAlign='middle'>
									<Image className='shadowimage' centered src={require('../Images/makinmovies.jpg')}/>
								</Grid.Column>
								<Grid.Column verticalAlign='middle'>
									<Image className='shadowimage' centered src={require('../Images/chinawhitegun.JPG')}/>
								</Grid.Column>
							</Grid.Row>
							<Grid.Row columns={1}>
								<Grid.Column textAlign='center'>
									<p className="mainsize">
										Zack’s latest film, China White, was a subversion and critique of toxic
										masculinity commonly found in the revenge film. This socially conscious
										film was accepted into six film festivals and was named a semifinalist in four
										including the New York Cinematography Awards and the Los Angeles CineFest.
									</p>
								</Grid.Column>
							</Grid.Row>
							<Grid.Row columns={1}>
								<Grid.Column >
									<Image className='ui shadowimage' centered size='huge' src={require('../Images/zackjackpress.JPG')} />
								</Grid.Column>
							</Grid.Row>
						</Grid>
					</Container>
				</Container>
			</Segment>


			);
	}
}

export default Homepage;